<template>
    <Header :homeDisplay="this.homeDisplay"/>

    <b-container class="topcontainer" v-if="this.changepd===false">
        <!-- <b-row> -->
            <!-- <b-col cols="4"></b-col> -->
            <!-- <b-col cols="2"><b-button class="btn" variant="primary" to="/home" size="sm" v-if="this.define===false">홈</b-button></b-col> -->
            <!-- <b-col cols="3"><b-button class="btn" variant="primary" @click="onChangePassword" size="sm" v-if="this.define===false">암호변경</b-button></b-col> -->
            <b-button class="btn" variant="danger" @click="qdefine" size="sm" v-if="this.define===false">회원탈퇴</b-button>
            <!-- <b-col cols="2"></b-col> -->
        <!-- </b-row> -->
    </b-container>    
    <div class="d-flex justify-content-center" v-if="this.define===true">
        <b-button class="btn" variant="primary" @click="onCancle" size="lg" v-if="this.define===true">아니오</b-button>
        <b-button class="btn" variant="danger" @click="onDeleteUser(this.user)" size="lg" v-if="this.define===true">탈퇴 하겠습니까</b-button>
    </div>

    <div class="user_exit" v-if="this.define===false && this.changepd===false">
        <h2>사용자명 : {{this.user.username}}</h2>
        <h4>e-mail : {{this.user.email}}</h4>
    </div>
    <div class="user_exit" v-if="this.define===true">
        <h2>o</h2>
        <h4>o</h4>
    </div>    

    <div class="checktickets">
        <h3>[ 기간동안 식수 확인 ]</h3>
        <form @submit.prevent="selectDateSubmit" class="mainlist">
            <v-date-picker v-model.range.number="date_range" :masks="masks" :max-date='maxDate'>
                <template v-slot="{ inputValue, inputEvents }">
                    <div class="flex justify-center items-center">
                        <!-- <input
                            :value="inputValue.start"
                            v-on="inputEvents.start"
                            class="border px-2 py-1 w-25 rounded focus:outline-none focus:border-indigo-200"
                        />      
                        <input
                            :value="inputValue.end"
                            v-on="inputEvents.end"
                            class="border px-2 py-1 w-25 rounded focus:outline-none focus:border-indigo-200"
                        />
                        <b-button type="submit" class="btn" variant="primary">설 정</b-button> -->

                        <div class="input-group mb-3">
                            <input type="text" class="form-control" :value="inputValue.start" v-on="inputEvents.start" aria-describedby="button-setting1">
                            <input type="text" class="form-control" :value="inputValue.end" v-on="inputEvents.end" aria-describedby="button-setting1">
                            <button type="submit" class="btn btn-primary" id="button-setting">설정</button>
                        </div>
                    </div>                
                </template>
            </v-date-picker>
        </form>

        <div class="mb-2 border bg-warning border-warning rounded p-2" style="--bs-bg-opacity: 0.2" v-if="this.totalticket!=0">
            <b-container>
                    <span class="eat"> 총 : {{this.totalticket}} 일</span>
                    <span style="margin-left: 40%;">
                        <download-excel class="btn btn-primary" :data="this.dtickets" :fields="tickets_fields" 
                                worksheet="식수" :name="this.excelfname" size="lg" >내려받기(엑셀)</download-excel>
                    </span>                    
            </b-container>
        </div>
        
        <div table-responsive class="userman" v-if="this.s_display && this.totalticket!=0">
            <table class="table table-sm table-hover">
                <thead class="thead-dark">
                    <tr>
                        <th scope="col">순번</th>
                        <th scope="col">일자</th>                        
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(ticket, index) in this.dtickets" :key="ticket.id">
                        <td scope="row">{{index+1}}</td>
                        <td scope="row">{{ticket.id}}</td>                        
                    </tr>
                </tbody>
            </table>
        </div>

    </div>
</template>

<script>
import {ref} from 'vue';
import router from "@/router";
import JsonExcel from "vue-json-excel3";
import Header from "@/components/Header.vue";

const define = ref(false);
const changepd = ref(false);
const passwordNew = ref('');
const tickets = ref([]);
const dtickets = ref([]);
const totalticket= ref(0);
const now = new Date();
const date = new Date();
const mdate = new Date(date.setMonth(date.getMonth()-1));
const date_range = ref({
    start: mdate,
    end: now,
});
const maxDate= now;
const s_display=ref(false);
const excelfname=ref('');
const homeDisplay = ref(true);

export default{
    name: "UserInfo",
    components: {
        Header, 
        downloadExcel:JsonExcel
    },
    data() {
        return{
            user:'',
            define: define,
            changepd:changepd,
            passwordNew:passwordNew,
            date_range: date_range,
            masks: {
                input: 'YYYY-MM-DD',
            },
            maxDate:maxDate,
            tickets:tickets,
            totalticket:totalticket,
            s_display:s_display,
            excelfname:excelfname,
            tickets_fields: {
                "번호":"index",
                "식수일자": "id",
                "확정일자": "created_at",
            },
            dtickets:dtickets,
            homeDisplay:homeDisplay,
        }        
    },
    
    computed:{
        getTicketList() {
            return this.$store.state.tickets.ticketList;            
        },
    },    
    
    mounted() {
        this.user = this.$store.state.auth.user;
        this.$store.dispatch("fetchTicketList");
        this.totalticket=0;
        this.dtickets=[];
    },

    watch:{
        getTicketList(val){
            this.tickets = val;
            this.totalticket=0;
            this.dtickets=[];
        },        
    },
    
    methods: {
        
        qdefine() {
            this.define = this.define ?  false: true;
        },
        
        onDeleteUser(user) {
            this.$store.dispatch("fireBaseDeleteUser", user);             
            this.$store.dispatch("signOut");                    
        },
        
        onCancle() {
            this.define=false;
            router.push("/home");
        },        

        selectDateSubmit() {
            
            const today = new Date();
            const year = today.getFullYear();
            const month = today.getMonth()+1;
            const day = today.getDate();
            
            var todayString = year + "-";
            if (month < 10) {
                todayString += "0";
            }
            todayString += month + "-";
            if (day < 10) {
                todayString += "0";
            }
            todayString += day;
            this.excelfname = this.user.username+'_'+todayString+'_jejucafeteria.xls';            
            let useTicket=[];
            let count=0;

            this.tickets.sort((a,b) => a.id.localeCompare(b.id));

            this.tickets.forEach( (ticket) =>{
                let k_date = new Date(ticket.date);                
                if( (new Date(date_range.value.start) <= k_date ) && ( k_date <= new Date(date_range.value.end))) {
                    count+=1;
                    let ttdate = new Date(ticket.created_at).toLocaleString();                
                    useTicket.push({
                        index: count,
                        id:ticket.id,
                        created_at: ttdate,
                    });
                }                
            });

            this.dtickets = useTicket;
            this.totalticket = count;
            this.s_display = true;
        }
    },
}

</script>

<style scoped>
.topcontainer {
    padding: 20px;
    text-align: center;
}
.user_exit {
    padding: 40px;
    text-align: center;
}
.passregion{
    padding: 40px;
}

.checktickets{
    padding: 10px;
    text-align: center;
}
h3 {
    text-align: center;
}
.eat{
    color:blue;
}
.mainlist{
    padding: 10px;
}
</style>
